.create-resource-wrapper {
  margin: auto;
  position: relative;
  padding-top: 63px;
}
.bd {
  display: flex;
  justify-content: center;
  z-index: 9;
}
.ft {
  text-align: center;
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: white;
  border-top: 1px solid #dcdcdc;
  z-index: 99;
  padding: 10px 50px;
}
.ft .ft-btn {
  padding: 8px 40px;
  font-size: 14px;
}
.ft .gray-btn {
  color: #b7b7b7;
}
.ft .deep-color-btn {
  background-color: #333333;
}
.ft .lf-side {
  float: left;
}
.ft .rt-side {
  float: right;
}
